import React from "react-dom";
import { NavLink } from "react-router-dom";
import web from "../src/img/hero_img.svg";
const Common =(props) =>{
    return(
        <>
            <section id= "header" className="d-flex align-items-center">
            <div className = "container-fluid nav_bg">
                <div className="row">
                    <div className="col-10 mx-auto">
                    <div className="row d-flex align-items-center placeit">
                        <div className= "col-md-6 pt-5 pt-lg-0 order-2 order-lg-1">
                            <h1>{props.name} <strong className="my_brand">WebVlot</strong></h1>
                            <h2 className="my-3 mb-5">We are team of talented web developer making websites</h2>
                            <div className="mt-3">
                                <NavLink to = {props.visit} className = "btn-get-started"> {props.btname}</NavLink>
                            </div>
                        </div>
                        <div className= "col-lg-6 order-1 order-lg-2 header-img">
                            <img src ={props.imgsrc} className = "img-fluid animated " alt= "animated header img"/>
                        </div>
                        </div>
                    </div>
                </div>
            </div>
            </section>
        </>
    );
};
export default Common;