import React, { useState } from "react";
const Contact =() =>{
    const [data,setData] = useState({
        username : ' ',
        phone : " ",
        email : " ",
        msg : " ",
    });
    const InputEvent  = (event) =>{
        const{name, value} = event.target;
        setData((preval)=>{
            return{
                    ...preval,
                    [name]:value,
            };
        });
    };
    const formSubmit = (e)=>{
        e.preventDefault();
        alert(` your name is ${data.username} your phone number is ${data.phone} your email is ${data.email} and your message is ${data.msg} has been submitted!`);
    };
    return(
        <>
          <div className = "my-5">
              <h1 className = "text-center">Contact Us</h1>
          </div>
          <div className = "container contact_div">
              <div className = "row">
                <div className = "col-md-6 col-10 mx-auto">
                    <form onSubmit = {formSubmit}>
                        <div class="mb-3">
                            <label for="exampleFormControlInput1" class="form-label" rqquired>Name</label>
                            <input type="text" class="form-control" 
                            id="exampleFormControlInput1" 
                            name = "username"
                            value = {data.username}
                            onChange = {InputEvent}
                            placeholder="Enter your name"/>
                        </div>
                        <div class="mb-3">
                            <label for="exampleFormControlInput1" class="form-label" rqquired>Phone Number</label>
                            <input type="phone" class="form-control" 
                            id="exampleFormControlInput1" 
                            name = "phone"
                            value = {data.phone}
                            onChange = {InputEvent}
                            placeholder="Enter your Phone"/>
                        </div>
                        <div class="mb-3">
                            <label for="exampleFormControlInput1" class="form-label" rqquired>Email address</label>
                            <input type="email" class="form-control" 
                            id="exampleFormControlInput1" 
                            name = "email"
                            value = {data.email}
                            onChange = {InputEvent}
                            placeholder="name@example.com"/>
                        </div>
                        <div class="mb-3">
                            <label for="exampleFormControlTextarea1" class="form-label">Your Message</label>
                            <textarea class="form-control" 
                            id="exampleFormControlTextarea1"
                            name = "msg"
                            value = {data.m}
                            onChange = {InputEvent} 
                            rows="3"></textarea>
                        </div>
                        <div class="col-12">
                            <button class="btn btn-outline-primary" type="submit">Submit</button>
                        </div>
                    </form>
                </div>
              
            </div>
          </div>
        </>
    );
};
export default Contact;